<template>
    <div class="series" ref="mScroll">
        <ul>
            <li v-for="series in serieses">
                <img :src="series.img">
                <h2 class="title">{{series.name}}</h2> 
            </li>
        </ul>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    name: 'series',
    props: {
        data: {
            type: Object
        }
    },
    created () {
        this.serieses = this.data.serieses
        this.$nextTick(() => {
            this._initState()
        })
    },
    methods: {
        _initState () {
            if (!this.scroll) {
                 this.scroll = new BScroll(this.$refs.mScroll, {click: true})
            } else {
                this.scroll.refresh()
            }
        }
    },
    data () {
    return {
        serieses: []
    }
    }
}
</script>
<style lang="stylus" style="sheetstylus">
.series
    position absolute
    top 230px
    bottom 0px
    margin-bottom 10px
    z-index -1
.series ul
    display: -moz-box
    display: -ms-flexbox
    display: flex
    display: -webkit-box
    display: -webkit-flex
    -moz-flex-wrap: wrap
    -ms-flex-wrap: wrap
    -o-flex-wrap: wrap
    flex-wrap: wrap
    -webkit-flex-wrap: wrap
    margin-top 20px
    li
        width 33.33%
        -webkit-flex 1
        -ms-flex 1
        flex 1
        -moz-box-flex 1
        -webkit-box-flex 1
        text-align center
        height 200px
        img
            width 100px
            height 160px
            border-radius 20px
            margin-bottom 6px
</style>



