<template>
    <div class="carousel">
        <el-carousel :interval="4000" type="card" height="130px" arrow="always">
            <el-carousel-item v-for="item in srcs">
              <a href=""><img width="100%" height="130" :src="item"></a>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
import data from '../../assets/data.json'
export default {
  name: 'carousel',
  data () {
    return {
        srcs: []
    }
  },
  created () {
    this.srcs = data.head.src
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
.carousel
    width 100%
    position fixed
    top 46px
    padding-top 10px
    height 160px
    background-color #fff
    overflow hidden
    .el-carousel__item h3
        color #475669
        font-size 14px
        opacity 0.75
        margin 0
    .el-carousel__item:nth-child(2n)
        background-color #99a9bf
    .el-carousel__item:nth-child(2n+1)
        background-color: #d3dce6;
    .el-carousel__arrow
        top 40%
    .el-carousel__item
      width 64%
    .is-in-stage
      left -6%
</style>



