<template>
    <div class="v-head">
        <input class="search" type="text" placeholder="请输入影片名">
        <el-button type="primary" class="search-btn" icon="search">搜索</el-button>
    </div>
</template>

<script>
export default {
  name: 'head',
  data () {
    return {
    }
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
.v-head
    position fixed
    top 0
    height 36px
    width 100%
    padding 5px 5px 5px 0
    background #000
    text-align right
    overflow hidden
    .search
        height 30px
        padding-left 10px
        margin-right 10px
        border-radius 20px
        outline none
    .search-btn
        margin-right 10px
</style>



