<template>
    <div class="tab">
          <ul>
              <li>
                <router-link to="/movie">{{data.tab.tabSelect[0]}}</router-link>
              </li>
              <li>
                <router-link to="/series">{{data.tab.tabSelect[1]}}</router-link>
              </li>
              <li>
                <router-link to="/variety">{{data.tab.tabSelect[2]}}</router-link>
              </li>
              <li>
                <router-link to="/cartoon">{{data.tab.tabSelect[3]}}</router-link>
              </li>
          </ul>
    </div>
</template>

<script>
export default {
  name: 'tab',
  props: {
    data: {
      type: Object
    }
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
.tab
  position fixed
  width 100%
  top 206px
  background #fff
  ul
    display: -webkit-flex  /* 新版本语法: Chrome 21+ */
    display: flex          /* 新版本语法: Opera 12.1, Firefox 22+ */
    display: -moz-box      /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox   /* 混合版本语法: IE 10 */
    display: -webkit-box   /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
   li
    -webkit-flex: 1        /* Chrome */
    -ms-flex: 1             /* IE 10 */
    flex: 1                /* NEW, Spec - Opera 12.1, Firefox 20+ */
    -webkit-box-flex: 1     /* OLD - iOS 6-, Safari 3.1-6 */
    -moz-box-flex: 1       /* OLD - Firefox 19- */ 
    height 30px
    line-height 30px
    text-align center
   .active
     color red
</style>



