<template>
    <div class="detail" v-show="showFlag" ref="detailScroll">
        <div class="intrduce">
            <img class="intrduce-img" :src="selectedMovie.img" alt="">
            <div class="intrduce-text">
              <h1 class="movieName">片名：{{selectedMovie.name}}</h1>
              <p class="movieActor">演员：</p>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'detail',
  props: {
    selectedMovie: {
      type: Object
    }
  },
  methods: {
    show () {
        this.showFlag = true
        this.$nextTick(() => {
            if (!this.scroll) {
                 this.scroll = new BScroll(this.$refs.detailScroll, {click: true})
            } else {
                this.scroll.refresh()
            }
        })
        console.log(123)
    }
  },
  data () {
    return {
        showFlag: false
    }
  }
}
</script>
<style lang="stylus" style="sheetstylus">
.detail
    position absolute
    width 100%
    height 100%
    padding-top 20px
    left 0
    z-index 100
    top 0px
    background #fff
    .intrduce
      display flex
      .intrduce-img
        flex 0 0 110px
        width 110px
        height 150px
        margin 10px 40px 10px 10px
        border-radius 20px
      .intrduce-text
        flex 1
        padding-top 15px
</style>



