<template>
    <div class="movie" ref="mScroll">
        <ul>
            <li @click="selectMovie(movie, $event)" v-for="movie in movies">
                <img :src="movie.img">
                <h2 class="title">{{movie.name}}</h2>
            </li>
        </ul>
        <detail ref="detailShow" :selectedMovie="selectedMovie"></detail>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import detail from 'components/detail/detail'
export default {
    name: 'movie',
    props: {
        data: {
            type: Object
        }
    },
    created () {
        this.movies = this.data.movies
        this.$nextTick(() => {
            this._initState()
        })
    },
    methods: {
        _initState () {
            if (!this.scroll) {
                 this.scroll = new BScroll(this.$refs.mScroll, {click: true})
            } else {
                this.scroll.refresh()
            }
        },
        selectMovie (movie, event) {
            if (!event._constructed) {
                return
            }
            this.selectedMovie = movie
            this.$refs.detailShow.show()
        }
    },
    data () {
        return {
            movies: [],
            selectedMovie: {}
        }
    },
    components: {
        detail
    }
}
</script>
<style lang="stylus" style="sheetstylus">
.movie
    position absolute
    width 100%
    top 230px
    bottom 0px
    margin-bottom 10px
    z-index -1
.movie ul
    display: -moz-box
    display: -ms-flexbox
    display: flex
    display: -webkit-box
    display: -webkit-flex
    -moz-flex-wrap: wrap
    -ms-flex-wrap: wrap
    -o-flex-wrap: wrap
    flex-wrap: wrap
    -webkit-flex-wrap: wrap
    margin-top 20px
    li
        width 33.33%
        -webkit-flex 1
        -ms-flex 1
        flex 1
        -moz-box-flex 1
        -webkit-box-flex 1
        text-align center
        height 180px
        img
            width 100px
            height 140px
            border-radius 20px
            margin-bottom 6px
</style>



